/*-------- Banner --------*/

.banner { 
    position: relative;
    overflow: hidden;
    z-index: 3;

    &__wrapper {
        padding-top: 129px;
        padding-bottom: 202px;
        position: relative;
        z-index: 2;
    }

    &__title {
        margin-top: 85px;

        font-family: $font-b;
        font-size: 124px;
        color: #ffffff;
        line-height: 1.2;
    }

    &__title-text-mask {
        overflow: hidden;
    }

    &__title-text--blue {
        color: $blue;
    }

    &__text {
        display: flex;
        flex-wrap: wrap;
        margin-top: 46px;

        max-width: 671px;
        font-family: $font-r;
        font-size: 30px;
        color: #ffffff;
        line-height: 1.3;

        overflow: hidden;
        white-space: pre;
    }

    &__buttons {
        display: flex;
        margin-top: 80px;
    }

    &__button:not(:first-child) {
        margin-left: 18px;
    }
}

@media (max-width: 1499px) {

    .banner {

        &__wrapper {
            padding-bottom: 150px;
        }

        &__title {
            margin-top: 60px;
            font-size: 110px;
        }
    }
}

@media (max-width: 1399px) {

    .banner {

        &__title {
            font-size: 80px;
        }

        &__text {
            margin-top: 20px;
            font-size: 25px;
        }

        &__buttons {
            margin-top: 60px;
        }
    }
}

@media (max-width: 1219px) {

    .banner {

        &__title {
            font-size: 64px;
        }

        &__text {
            font-size: 22px;
        }
    }
}

@media (max-width: 1099px) {

    .banner {

        &__wrapper {
            padding-top: 110px;
        }
    }
}

@media (max-width: 989px) {

    .banner {
        height: 100vh;
        display: flex;
        align-items: center;

        &__wrapper {
            padding-top: 90px;
        }

        &__title {
            margin-top: 40px;
            font-size: 50px;
        }

        &__text {
            margin-top: 15px;
            font-size: 18px;
        }

        &__buttons {
            margin-top: 30px;
        }
    }
}

@media (max-width: 619px) {

    .banner {

        &__wrapper {
            padding-bottom: 100px;
        }
    }
}

@media (max-width: 479px) {

    .banner {

        &__wrapper {
            padding-bottom: 60px;
        }

        &__title {
            margin-top: 10px;
            font-size: 43px;
        }

        &__text {
            font-size: 15px;
        }
    }
}