/*-------- Case --------*/

.case {
    padding-top: 106px;
    padding-bottom: 64px;
    overflow: hidden;
    position: relative;

    &__wrapper {
        display: flex;
        align-items: flex-start;
        position: relative;
        z-index: 1;
    }

    &__button {
        display: inline-flex;
        height: 52px;
        padding: 0 43px;
        margin-top: 70px;
    }

    &__list {
        margin-top: 80px;
    }

    &__item:not(:first-child) {
        margin-top: 45px;
    }

    &__item-title {
        font-family: $font-sb;
        color: $blue-dark;
    }

    &__item-text {
        margin-top: 12px;
        line-height: 1.4;
    }

    &__column {
        flex-shrink: 0;
        width: 37%;
        padding-right: 58px;
        z-index: 12;
    }

    &__title {
        z-index: 1;
        position: relative;
    }
}

@media (max-width: 989px) {

    .case {
        padding-bottom: 104px;

        &__wrapper {
            flex-direction: column;
        }

        &__column {
            width: 100%;
            padding-right: 0;
            margin-bottom: 40px;
        }
    }
}

@media (max-width: 619px) {

    .case {
        padding-top: 60px;
    }
}

@media (max-width: 479px) {

    .case {

        &__list {
            margin-top: 40px;
        }

        &__button {
            margin-top: 40px;
        }
    }
}