/*-------- Industry --------*/

.industry {
    margin-top: 117px;
    padding-bottom: 87px;
    position: relative;

    &__wrapper {
        position: relative;
        z-index: 2;
    }

    &__column {
        max-width: 582px;
    }

    &__title {
        font-family: $font-sb;
        font-size: 50px;
        line-height: 1.4;
    }

    &__background {
        width: 100%;

        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
    }

    &__card {
        width: 703px;
        height: 563px;

        background-color: #000000;
        border-radius: 5px;
    }
}

@media (max-width: 989px) {

    .industry {
        margin-top: 50px;
        padding-bottom: 60px;

        &__card {
            width: 500px;
            height: 441px;
        }

        &__title {
            font-size: 35px;
        }
    }
}

@media (max-width: 767px) {

    .industry {

        &__card {
            width: 400px;
            height: 341px;
        }

        &__title {
            font-size: 29px;
            padding-bottom: 10px;
        }
    }
}

@media (max-width: 479px) {

    .industry {

        &__card {
            height: 340px;
            width: 100%;
        }
    }
}