/*-------- Header --------*/

.header {
    width: 100%;

    position: fixed;
    left: 0;

    z-index: 11;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        position: relative;

        height: 129px;
    }

    &__menu-list {
        display: flex;
    }

    &__menu-item:not(:first-child) {
        margin-left: 49px;
    }

    &__menu-link {
        display: block;
        position: relative;
        overflow: hidden;
        padding-bottom: 5px;
        font-family: $font-b;
        color: #000000;
    }

    &__menu-link::after {
        display: block;
        content: "";
        height: 1px;
        background-color: $blue;
        position: absolute;
        bottom: 0;
        width: 100%;
        transform: translateX(-101%);
    }

    &__menu-link:hover::after {
        transition: transform 0.3s ease-out;
        transform: translateX(0);
    }

    &__menu-link.animate-out::after {
        transition: transform 0.3s ease-out;
        transform: translateX(101%);
    }

    &__menu-link:hover,
    &__menu-link.active {
        color: $blue;
    }

    &__button .button__text {
        color: $blue-dark;
    }

    &__button, &__mobile-button {
        transition: opacity 0.3s ease-out, transform 0.3s ease-out, visibility 0.3s ease-out;
    }

    &.has-scrolled .logo {
        opacity: 0;
        visibility: hidden;
        transform: translateX(-30%);
    }

    &.has-scrolled &__button, &.has-scrolled &__mobile-button {
        opacity: 0;
        visibility: hidden;
        transform: translateX(30%);
    }

    &__mobile-button {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 30px;
        height: 30px;

        position: relative;

        z-index: 2;
    }

    &__mobile-icon {
        width: 100%;
        height: 100%;
        fill: $blue-dark;
    }

    &__mobile-button.opened &__mobile-icon {
        display: none;
    } 
    
    &__mobile-button.opened:after, &__mobile-button.opened:before {
        content: '';
        width: 4px;
        height: 100%;
        background-color: #ffffff;
        position: absolute;
        border-radius: 4px;
    }

    &__mobile-button.opened:after {
        transform: rotateZ(45deg);
    }

    &__mobile-button.opened:before {
        transform: rotateZ(-45deg);
    }
}

.header {

    &--main &__menu-link {
        color: #ffffff;
    }

    &--main &__button .button__text {
        color: #ffffff;
    }

    &--main &__mobile-icon {
        fill: #ffffff;
    }
}

@media (max-width: 1399px) {

    .header {

        &__menu-item:not(:first-child) {
            margin-left: 28px;
        }
    }
}

@media (max-width: 1099px) {

    .header {

        &__wrapper {
            height: 90px;
        }

        &__menu-link {
            font-size: 14px;
        }
    }
}

@media (max-width: 989px) {

    .header {

        &__menu {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
        }

        &__menu:before {
            content: '';

            display: block;
            width: 100%;
            height: 100%;

            background-color: #034165;

            opacity: 0;
            transition: opacity 0.1s ease-out;
        }

        &__menu.opened:before {
            opacity: 1;
        }

        &__menu-list {
            flex-direction: column;
            height: 0;

            background-color: #ffffff;
            box-shadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.3);
            z-index: 1;
            overflow: hidden;
            transition: height 0.2s ease-out;
        }

        &__menu-item:not(:first-child) {
            margin-left: 0; 
        }

        &__menu-item {
            border-top: 1px solid #648da514;
        }

        &__menu-link {
            display: flex;
            align-items: center;
            padding: 0 20px;
            height: 40px;

            color: #343434;
        }

        &__menu-link:after {
            content: none;
        }

        &__button {
            margin-left: auto;
            margin-right: 30px;
        }
    }
}

@media (max-width: 479px) {

    .header {

        &__wrapper {
            height: 70px;
        }

        &__button.button {
            height: 35px;
            padding: 0 10px;
            font-size: 10px;
            margin-right: 18px;
        }

        &__button .button__icon {
            width: 15px;
            height: 15px;
            margin-right: 5px;
        }
    }
}