/*-------- Particles --------*/

.particles {
    position: absolute;
    z-index: 0;
}

.particles {

    &--reputation {
        width: 1000px;
        top: -155px;
        right: -252px;
        z-index: 1;
        transform: rotateZ(47deg);
    }
}

.particles {

    &--case {
        top: 0;
        right: 0;
        width: 1000px;
        top: -200px;
    }
}

@media (max-width: 1099px) {

    .particles {

        &--reputation {
            width: 83%;
        }

        &--case {
            width: 100%;
        }
    }
}

@media (max-width: 767px) {

    .particles {

        &--reputation {
            top: 0;
        }

        &--case {
            width: 80%;
            top: 0;
        }
    }
}

@media (max-width: 619px) {

    .particles {

        &--reputation {
            right: -45px;
        }
    }
}

@media (max-width: 479px) {

    .particles--reputation {
        width: 73%;
    }
}