/*-------- Basic --------*/

* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

a, button {
    cursor: pointer;
}

body {
    font-family: $font-r;
    color: #000000;

    &.no-scroll {
        overflow: hidden !important;
    }
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
}

// .swiper-wrapper {
//     width: 603px !important;
// }

button {
    background-color: transparent;
    border: none;
    padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 20px;
}

.container--header {
    max-width: 1649px;
}

.container {

    &--blog {
        max-width: 918px;
    }
}

.text-mask {
    display: flex;
    overflow: hidden;
}

.side-text {
    pointer-events: none;
    position: absolute;
    right: calc(100% + 80px);
    top: 0;

    font-family: $font-b;
    color: rgba(0, 0, 0, 0.07);
    font-size: 76px;
    transform: rotateZ(180deg);
    writing-mode: vertical-rl;

    white-space: nowrap;
    z-index: 12;

    &--services {
        top: 211px;
    }

    &--industry {
        top: 74px;
    }

    &--case {
        top: 20px;
    }
}

.background {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 0;
    z-index: -1;

    transition: opacity 0.2s ease-out;

    &.active {
        opacity: 1;
        z-index: 10;
    }
}

@media (max-width: 1499px) {

    .side-text {
        right: calc(100% + 7px);
    }
}

@media (max-width: 1399px) {

    .side-text {
        right: 97.5%;
    }

    .side-text {

        &--blog {
            right: 103%;
        }
    }
}

@media (max-width: 1219px) {

    .side-text {
        font-size: 62px;
        right: unset;
        left: 0;
    }

    .side-text {

        &--blog {
            left: unset;
            right: 100%;
        }
    }
}

@media (max-width: 1099px) {

    .side-text {

        &--blog {
            display: none;
        }
    }
}

@media (max-width: 989px) {

    .side-text--case {
        top: 599px;
    }
}

@media (max-width: 767px) {

    .side-text {
        display: none;
    }
}

@media (max-width: 359px) {

    .container {
        padding: 0 10px;
    }
}