/*-------- Blog --------*/

.blog {
    padding-top: 129px;
    padding-bottom: 98px;

    &__banner {
        position: relative;
    }

    &__banner-image-wrapper {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        max-height: 100%;
    }

    &__wrapper {
        position: relative; 
    }

    &__banner-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;

        position: relative;
    }

    &__banner-wrapper:before {
        content: "";
        display: block;
        padding-top: 83%;
    }

    &__banner-title {
        display: flex;
        align-items: center;
        justify-content: center;

        padding: 60px 44px;

        position: absolute;

        color: #ffffff;
        font-size: 60px;
        text-align: center;

        background-color: rgba(0, 0, 0, 0.6);
    }

    &__content {
        padding-top: 72px;
        opacity: 0;
    }

    &__title {
        font-family: $font-sb;
        font-size: 27px;
    }

    &__text {
        font-size: 19px;
        line-height: 1.5;
    }

    &__title + &__text {
        margin-top: 59px;
    }

    &__image {
        width: 100%;
        margin-top: 50px;
        margin-bottom: 50px;
    }

    &__nav {
        display: flex;
        justify-content: space-between;
        margin-top: 125px;
    }

    &__nav-button {
        width: 49%;
        height: 288px;

        font-family: $font-sb;
        font-size: 19px;
        color: #ffffff;
    }

    &__nav-button-wrapper {
        display: flex;
        justify-content: center;
        flex-direction: column;

        width: 100%;
        height: 100%;
        padding: 80px;

        position: relative;

        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        border-radius: 5px;
        opacity: 0;
    }

    &__nav-button-wrapper:before {
        content: "";
        position: absolute;
        z-index: 0;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #000000;
        opacity: 0.4;
        transition: opacity 0.2s ease-out;
    }

    &__nav-button:hover &__nav-button-wrapper:before,
    &__nav-button.active &__nav-button-wrapper:before {
        opacity: 0;
    }

    &__nav-button--prev &__nav-button-wrapper {
        align-items: flex-end;
    }

    &__nav-icon {
        width: 17px;
        height: 17px;
        fill: #ffffff;
    }

    &__nav-title {
        display: flex;
        align-items: center;
        z-index: 1;
    }

    &__nav-button--prev &__nav-icon {
        transform: rotateZ(180deg);
    }

    &__nav-icon + &__nav-title-text,
    &__nav-title-text + &__nav-icon {
        margin-left: 32px;
    }

    &__nav-title-text {
        position: relative;
    }

    &__nav-title-text:after {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        background-color: #ffffff;
        top: 100%;
        left: 0;
    }

    &__nav-text {
        margin-top: 28px;
        z-index: 1;
    }
}

@media (max-width: 1219px) {

    .blog {

        &__banner-title {
            font-size: 49px;
        }

        &__banner-wrapper:before {
            padding-top: 56%;
        }
    }
}

@media (max-width: 1099px) {

    .blog {
        padding-top: 90px;

        &__banner:before {
            padding-top: 0;
        }

        &__banner {
            height: calc(100vh - 90px);
        }
    }
}

@media (max-width: 989px) {

    .blog {

        &__banner-title {
            padding: 40px;
            font-size: 33px;
        }

        &__nav-button-wrapper {
            padding: 40px;
        }

        &__nav-button {
            height: 240px;
        }
    }
}

@media (max-width: 767px) {

    .blog {
        padding-bottom: 60px;

        &__banner-title {
            font-size: 27px;
        }

        &__nav {
            margin-top: 80px;
        }

        &__nav-button-wrapper {
            padding: 20px;
        }

        &__nav-button {
            height: 195px;
            font-size: 18px;
        }

        &__nav-text {
            margin-top: 20px;
        }
    }
}

@media (max-width: 619px) {

    .blog {

        &__banner-title {
            font-size: 20px;
        }

        &__title + &__text {
            margin-top: 30px;
        }

        &__text {
            font-size: 16px;
        }

        &__title {
            font-size: 22px;
        }

        &__image {
            margin-top: 30px;
            margin-bottom: 30px;
        }

        &__nav-button {
            height: 160px;
            font-size: 13px;
        }
    }
}

@media (max-width: 479px) {

    .blog {

        &__banner-title {
            padding: 20px;
            font-size: 8vw;
        }

        &__nav {
            flex-direction: column;
        }

        &__nav-button {
            width: 100%;
            height: 195px;
        }

        &__nav-button:not(:first-child) {
            margin-top: 20px;
        }

        &__nav-button-wrapper {
            align-items: center !important;
        }
    }
}