/*-------- Card --------*/

.card {
    color: #ffffff;

    &__text {
        font-size: 15px;
    }

    &__title {
        font-size: 27px;
    }

    &__link {
        cursor: pointer;
    }

    &__rating {
        display: inline-flex;
        padding: 7px;
        background-color: #fef6e9;
        border-radius: 5px;
    }

    &__rating-item:not(:first-child) {
        margin-left: 7px;
    }

    &__rating-icon {
        width: 18px;
        height: 18px;
        fill: #fcaa3c;
    }
}

.card {

    &--case {
        display: flex;
        width: 603px;

        position: relative;

        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        border-radius: 5px;
        overflow: hidden;
        cursor: inherit;
    }

    &--case:before {
        content: '';
        display: block;
        padding-top: 82%;
    }

    &--case:after {
        content: '';
        width: 100%;
        height: 100%;

        position: absolute;
        top: 0;
        left: 0;

        background: linear-gradient(transparent, rgba(0, 0, 0, 0.6));

        transition: opacity 0.2s ease-in-out;
    }

    &--case:hover:after {
        opacity: 0.5;
    }

    &--case &__wrapper {
        display: flex;
        justify-content: flex-end;
        flex-direction: column;

        width: 100%;
        height: 100%;
        padding: 50px 76px;

        position: absolute;
        top: 0;
        left: 0;

        z-index: 2;
    }

    &--case &__link {
        display: flex;
        align-items: center;

        font-size: 15px;
        color: #ffffff;
    }

    &--case &__link-text {
        position: relative;
    }

    &--case &__link-text:before {
        content: '';

        width: 100%;
        height: 1px;
        background-color: #ffffff;

        position: absolute;
        top: 100%;
        margin-top: 5px;
    }

    &--case .arrow:before {
        background-color: #ffffff;
    }

    &--case .arrow:after {
        border-color: #ffffff;
    }

    &--case &__title {
        margin-top: 17px;
        margin-bottom: 23px;
    }
}

.card {

    &--testimonials {
        padding-top: 27px;
        padding-left: 40px;
        padding-right: 40px;
        padding-bottom: 45px;

        color: #000000;
        text-align: center;

        border-radius: 5px;
        box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.15);
        background-color: #ffffff;
    }

    &--testimonials &__image {
        width: 90px;
        height: 90px;
    }

    &--testimonials &__text {
        min-height: 69px;
        margin-top: 25px;
        font-size: 18px;
        line-height: 1.3;
    }

    &--testimonials &__rating {
        margin-top: 14px;
    }

    &--testimonials &__title {
        margin-top: 40px;
        font-size: 19px;
    }
}

.card {

    &--news {
        display: block;
        border-radius: 5px;
        box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.15);
        background-color: #ffffff;
        overflow: hidden;
    }

    &--news &__wrapper {
        padding-top: 35px;
        padding-left: 23px;
        padding-right: 23px;
        padding-bottom: 35px;
    }

    &--news &__background {
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }

    &--news &__background:before {
        content: '';
        display: block;
        padding-top: 59%;
    }

    &--news &__image {
        width: 286px;
    }

    &--news &__date-icon {
        width: 20px;
        height: 20px;
        fill: #d5d5d5;
        margin-right: 10px;
    }

    &--news &__date {
        display: flex;
        align-items: center;

        margin-top: 20px;

        font-family: $font-l;
        color: #d5d5d5;
    }

    &--news &__title {
        font-family: $font-sb;
        line-height: 40px;
        color: #000000;
        line-height: 1.3;
    }
}

@media (max-width: 1499px) {

    .card {

        &--news &__title {
            font-size: 21px;
        }
    }
}

@media (max-width: 1099px) {

    .card {

        &--case {
            width: 550px;
        }
    }

    .card {

        &--testimonials {
            padding-left: 20px;
            padding-right: 20px;
            padding-bottom: 35px;
        }

        &--testimonials &__text {
            min-height: 60px;
            font-size: 15px;
        }

        &--testimonials &__title {
            margin-top: 25px;
            font-size: 16px;
        }

        &--testimonials &__rating-icon {
            width: 14px;
            height: 14px;
        }

        &--testimonials &__image {
            width: 70px;
            height: 70px;
        }
    }

    .card {

        &--news &__wrapper {
            padding-top: 20px;
            padding-left: 11px;
            padding-right: 11px;
            padding-bottom: 25px;
        }
    }
}

@media (max-width: 767px) {

    .card {

        &--case {
            width: 450px;
        }
    }
}

@media (max-width: 619px) {

    .card {
        
        &--case {
            width: 350px;
        }
        
        &--case &__wrapper {
            padding: 30px;
        }
    }
}

@media (max-width: 479px) {

    .card {

        &--case {
            width: 302px;
        } 
    }

    .card {

        &--testimonials {
            padding-left: 10px;
            padding-right: 10px;
        }

        &--testimonials &__text {
            min-height: 50px;
            font-size: 13px;
        }
    }
}